.portfolio__cont{
    background-image:url('../../assets/Paramo1.webp');
    background-size: cover;
    background-blend-mode: color;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    height: 100%;
    min-height: 500px;
    overflow-y: visible;
    position: relative;
    text-align: center;
    width: 100%; 
}
.portfolio__container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2.5;
}

.portfolio__itm{
    background: var(--color-varprimary);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);

}

.portfolio__itm:hover{
    background: var(--color-varsec);
    border-color: var(--color-varbckg);
}

.portfolio__itm:hover > .portfolio__itm-img{
    animation: wiggle infinite 2s linear;
}
.portfolio__itm:hover > h3{
    color:var(--color-varbckg)
}
.portfolio__itm-img{
    border-radius: 1.5rem;
    
    
}

.portfolio__itm h3{
    align-self: auto;
    margin: 1.5rem 0 2rem;
    color: var(--color-primary);
}

.portfolio__itm-img{
    display: flex;
    gap: 0rem;
    margin-bottom: 1rem;

}

/* ====================== Medium Devices/ Tablets  ========================*/
@media screen and (max-width:1024px){
    .portfolio__container{
        grid-template-columns: 1fr 1fr;
        gap: 1.2rem;
    }
    
}

/* ====================== Small Devices/ Phones  ========================*/
@media screen and (max-width:600px){
    .portfolio__container{
        grid-template-columns: 1fr;
        gap: 0.9rem;
    }
}